.subscription-title {
    color:#00ADB9;
}

.subscription-header {
    background-color:#ff5722;
    color:#ffffff;
    padding: 8px;
}

.subscription-header th {
    text-align:center;
    border-bottom: 1px solid #303841;
}

.subscription-header th.description {
    text-align:left;
    padding-left:20px;
}

.subscription-packages {
    font-weight: bold;
    color: #303841;
    padding:10px;
}

.subscription-packages td {
    text-align: center;
    border-bottom: 1px solid #00ADB5;
}
.subscription-packages td.description {
    text-align:left;
    padding-left:20px;
}

.subscription-details {
    text-align: center;
}

.subscription-details button {
    background-color:#00ADB5;
    color:#ffffff;
    border: 1px solid #ffffff;
    padding: 5px 20px;
}

.subscription-blank td {
    padding-top:50px;
    border-bottom: 1px solid #303841;
}

.package-text-total {
    color:#00ADB5;
    font-weight:bold;
}

.package-container-total {
    padding:5px 10px;
    border: 1px solid #303841;
    text-align: center;
}

#coupon {
    padding:3px 2px;
    width:120px;
    margin-left:10px;
    border: 1px solid #303841;
}

#verify-coupon {
    width:120px;
    background-color: #ff5722;
    border: none;
    color:#fff;
    margin-top:5px;
    padding:3px 2px;
}

.subs-currency {
    padding-top:7px;
    font-size: 12pt;
    font-weight: bold;
}

.currencies {
    border-radius:0;
    border-color:#000000;
    margin-top:10px;
}

.subscription-save {
  font-family: Arial;
  color: white;
  font-size: 16px;
  background-color: #00ADB5;
  padding: 8px 15px 8px 15px;
  text-decoration: none;
  border-radius: 0px;
  margin-top: 10px;
}

.btn-paypal {
  display:inline;
}

.subs-message {
  color: #00ADB5;
  text-align: right;
  padding: 5px;
  font-size: 12pt;
  font-weight: bold;
}

.text-right {
  text-align: right;
}

#verifycoupon {
  position: relative;
  top: -1px;
}
